	module.exports = "/* RemoteStorage widget style */\n \n.rs-widget * {\n  box-sizing: border-box;\n}\n\n.rs-widget {\n  font-family: arial, sans-serif;\n  max-width: 350px;\n  color: #333;\n  background-color: #fff;\n  border-radius: 3px;\n  padding: 10px;\n  margin: 10px;\n  overflow: hidden;\n  z-index: 21000000; /* Make sure we're on a reasonably high visibility layer */\n  box-shadow: 0 1px 2px 0 rgba(0,0,0,0.1), 0 3px 8px 0 rgba(0,0,0,0.2);\n  will-change: max-height, height, width, opacity, max-width, background, box-shadow;\n  transition-property: width, height, opacity, max-width, max-height, background, box-shadow;\n  transition-duration: 300ms;\n}\n\n.rs-box {\n  overflow: hidden;\n  will-change: height;\n  transition-property: height, width, max-height;\n  transition-duration: 300ms;\n  transition-timing-function: ease-in;\n  opacity: 0;\n  max-height: 0px;\n}\n\n.rs-box.selected {\n  opacity: 1;\n  max-height: 400px;\n}\n\n/* Main logo */\n.rs-main-logo {\n  float: left;\n  margin-right: 0.625em;\n  transition: margin-left 300ms ease-out, transform 300ms ease-out;\n  height: 40px;\n  width: auto;\n  cursor: pointer;\n}\n\n\npolygon.rs-logo-shape {\n  fill: #FF4B03;\n  transition-property: fill;\n  transition-duration: 0.5s;\n}\n\n\n.rs-state-offline polygon.rs-logo-shape {\n  fill: #888;\n  transition-property: fill;\n  transition-duration: 0.5s;\n}\n\n\n\n/* Hide everything except logo when connected and clicked outside of box  */\n.rs-state-close {\n  max-width: 55px;\n  background-color: transparent;\n  box-shadow: none;\n  opacity: 0.5;\n}\n\n.rs-state-close:hover {\n  cursor: pointer;\n  opacity: 1;\n}\n\n /*Initial Connect remote storage box  */\n.rs-box-initial:hover {\n  cursor: pointer;\n}\n\n.rs-widget a {\n  color: #0093cc;\n}\n\n\n /*HEADLINE */\n.rs-small-headline {\n  font-size: 1em;\n  font-weight: bold;\n  margin: 0;\n  margin-bottom: 2px;\n  height: 1.1em;\n  word-break: break-all;\n  overflow: hidden;\n}\n\n.rs-sub-headline {\n  word-break: break-all;\n  overflow: hidden;\n  color: #666;\n  font-size: 0.92em;\n  height: 1.2em;\n}\n.rs-big-headline {\n  font-size: 1.625em;\n  font-weight: normal;\n  text-align: center;\n}\n\n\n\n/** BUTTONS  */\n.rs-button {\n  font: inherit;\n  color: inherit;\n  background-color: transparent;\n  border:1px solid #dcdcdc;\n  border-radius: 3px;\n  cursor: pointer;\n}\n.rs-button-small {\n  padding: 0.5em;\n  margin-left: 0.6em;\n  transition: border-color 300ms ease-out;\n}\n.rs-button-wrap {\n  margin-top: 10px;\n}\n\n.rs-button-wrap img,\n.rs-button-wrap svg {\n  float: left;\n  margin-right: 0.6em;\n  width: 40px;\n  height: 40px;\n}\n\n.rs-button-big {\n  padding: 15px 10px;\n  margin-bottom: 10px;\n  display: block;\n  width: 100%;\n  text-align: left;\n  transition: box-shadow 200ms;\n}\n.rs-button-big > div {\n  font-size: 1.125em;\n  padding: 10px 0;\n}\n.rs-button-big:hover {\n  box-shadow: 0 1px 2px 0 rgba(0,0,0,0.1), 0 3px 8px 0 rgba(0,0,0,0.2);\n}\n.rs-button-big:active {\n  background-color: #eee;\n  box-shadow: 0 1px 2px 0 rgba(0,0,0,0.1), 0 3px 8px 0 rgba(0,0,0,0.2);\n}\n.rs-button-big:last-child {\n  margin-bottom: 0;\n}\n\n.rs-content {\n  padding: 0 10px 10px 10px;\n}\n\n\n.rs-state-choose .rs-main-logo,\n.rs-state-sign-in .rs-main-logo {\n  margin-left: 45%;\n  float: none;\n}\n\n\n.rs-sign-in-form input[type=text] {\n  padding: 15px 10px;\n  display: block;\n  width: 100%;\n  font: inherit;\n  border: 1px solid #aaa;\n}\n.rs-sign-in-form input[type=submit] {\n  padding: 15px 10px;\n  margin-top: 20px;\n  margin-bottom: 15px;\n  display: block;\n  width: 100%;\n  border: none;\n  border-radius: 3px;\n  background-color: #3fb34f;\n  font: inherit;\n  color: #fff;\n  transition: box-shadow 200ms, background-color 200ms;\n}\n.rs-sign-in-form input[type=submit]:hover {\n  cursor: pointer;\n  background-color: #4BCB5D;\n  box-shadow: 0 1px 2px 0 rgba(0,0,0,0.1), 0 3px 8px 0 rgba(0,0,0,0.2);\n}\n.rs-sign-in-form input[type=submit]:active {\n  background-color: #3fb34f;\n}\n\n.rs-sign-in-error.hidden,\n.rs-box-error.hidden {\n  height: 0;\n}\n\n.rs-sign-in-error.visible,\n.rs-box-error.visible {\n  height: auto;\n  border-radius: 3px;\n  padding: 0.5em 0.5em;\n  margin-top: 0.5em;\n  text-align: center;\n  background-color: rgba(255,0,0,0.1);\n  color: darkred;\n}\n\n /*Choose provider box */\n.rs-box-choose {\n  text-align: center;\n  overflow: hidden;\n}\n.rs-box-choose .rs-big-headline {\n  margin-top: 20px;\n  margin-bottom: 20px;\n}\n.rs-box-choose p {\n  margin-top: 0;\n  margin-bottom: 20px;\n}\n\n/*Connected box */\n.rs-box-connected {\n  height: 40px;\n}\n.rs-connected-text {\n  float: left;\n}\n.rs-box-connected .rs-user {\n  font-weight: bold;\n}\n.rs-connected-buttons {\n  float: right;\n}\n.rs-disconnect:hover {\n  border-color: #FF2D2D;\n}\n.rs-disconnect:hover .rs-icon{\n  fill: #FF2D2D;\n}\n\n /*Floating widget styles */\n/*.rs-floating {\n  position: fixed;\n  top: 0;\n  right: 0;\n}\n*/\n\n\n/* used during sync \n@keyframes rotate {\n  0% {\n    transform: rotate(0deg);\n  }\n  100% {\n    transform: rotate(360deg);\n  }\n}\n\n\n.rs-state-close {\n  transition: max-height 100ms ease-out 0ms, max-width 300ms ease-out 300ms, background 300ms ease-in 200ms, opacity 300ms ease 200ms; \n}\n\n\n/* Mobile styles \n@media screen and (max-width: 360px) {\n  .rs-widget {\n    font-size: 100%;\n    padding: 10px;\n    transition: all 300ms ease-out;\n  }\n  .rs-floating {\n    position: relative;\n    top: auto;\n    right: auto\n  }\n  .rs-state-choose,\n  .rs-state-sign-in {\n    position: fixed;\n    top: 0;\n    margin: 0;\n    border-radius: 0;\n    height: 100%;\n    max-width: 100%;\n  }\n  .rs-state-choose .rs-close,\n  .rs-state-sign-in .rs-close {\n    display: block;\n  }\n}\n"

